.mc {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  .alertBox {
      width: 1000px;
      min-height: 500px;
      background: rgb(255, 255, 255);
      position: absolute;
      transform: translate(-50%, -50%);
      left: 50%;
      top: 50%;

      .top {
          width: 100%;
          height: 40px;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          border-bottom: 1px solid rgb(228, 228, 228);
          position: fixed;
          top: 0;

          p {
              margin: 0 20px;
          }

          p:nth-of-type(1) {
              font-size: 14px;
              font-weight: 600;
          }

          .iconfont {
              font-size: 18px;
              padding-right: 20px;
          }
      }

      .main {
          width: 100%;
          margin: 60px 0 70px ;
          padding: 0 20px;
          box-sizing: border-box;
          font-size: 14px;
          
          ul ,div,li{
              margin: 0 20px;
              display: flex;
              align-items: center;
          }
          input{
              font-size: 12px;
              padding-left: 8px;
              width: 260px;
              height: 30px;
              outline: none;
              border: 1px solid lightgray;
              border-radius: 3px;
              margin: 7px 0 7px 15px;
          }
      }

      .bot {
         
          width: 100%;
          height: 50px;
          display: flex;
          justify-content: flex-end;
          border-top: 1px solid rgb(228, 228, 228);
          position: fixed;
          bottom: 0;
          box-sizing: border-box;
          p {
              margin: 6px 0 ;
          }

          p:nth-of-type(1) {
              width: 80px;
              height: 33px;
              border: 1px solid rgb(216, 216, 216);
              border-radius: 5px;
              font-size: 14px;
              text-align: center;
              line-height: 33px;
              margin-right: 20px;
          }

          p:nth-of-type(2) {
              width: 80px;
              height: 33px;
              background: rgb(56, 74, 128);
              border-radius: 5px;
              color: white;
              font-size: 14px;
              text-align: center;
              line-height: 33px;
             margin-right: 20px;

          }

      }
  }
}